let typeLang;
let language;
 const getLanguage = async () => {
      language = await AsyncStorage.getItem('language');
      console.log('lang ', language);
      if (language == 'null') language = 'vi'

  };

export const lang = (key)=>{
	if(!language){
		language = 'vi'
		getLanguage();
	}
	var typeLang = 'fitness_'+language

	if(CONFIG_lang[typeLang] && CONFIG_lang[typeLang][key]) return CONFIG_lang[typeLang][key]
	else return key

}


